<template>
  <div
    class="grid-img-box"
    id="masonry-grid3"
    v-if="this.baseData.photoOthers.length!=0&&showBox"
  >
  <viewer :images="photoData">
    <div
      class="grid-img-item"
      v-for="(i,index) in photoData"
      :key="index"
    >
      <img
        :src="i.src"
        :alt="i.title"
      />
      <span
        v-text="i.title"
        class="title"
      ></span>
      <span v-text="i.desc"></span>
    </div>
  </viewer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      photoData: this.baseData.photoOthers,
      showBox: false,
    };
  },
  mounted() {
    var container3 = document.querySelector('#masonry-grid3');
    var msnry = new Masonry(container3,{
      columnWidth: 20,
      itemSelector: ".grid-img-item", // 要布局的网格元素
      gutter: 20, // 网格间水平方向边距，垂直方向边距使用css的margin-bottom设置
      percentPosition: true, // 使用columnWidth对应元素的百分比尺寸
      stamp: ".grid-stamp", // 网格中的固定元素，不会因重新布局改变位置，移动元素填充到固定元素下方
      fitWidth: true, // 设置网格容器宽度等于网格宽度，这样配合css的auto margin实现居中显示
      originLeft: true, // 默认true网格左对齐，设为false变为右对齐
      originTop: true, // 默认true网格对齐顶部，设为false对齐底部
      containerStyle: { position: "relative" }, // 设置容器样式
      transitionDuration: "0.8s", // 改变位置或变为显示后，重布局变换的持续时间，时间格式为css的时间格式
      stagger: "0.03s", // 重布局时网格并不是一起变换的，排在后面的网格比前一个延迟开始，该项设置延迟时间
      resize: true, // 改变窗口大小将不会影响布局
      initLayout: true,
    });
    const loading = this.$loading({
      lock: true,
      text: "加载中请稍后",
      spinner: "el-icon-loading",
      background: "rgba(199, 203, 189, 1)",
    });
    setTimeout(() => {
      this.showBox = true;
      loading.close();
    }, 2000);
  },
  methods: {},
};
</script>

<style lang="scss" src="@/assets/scss/photoOthers.scss" scoped>
</style>


